import { Component, OnInit, Renderer2, ElementRef, NgZone, Injector, ViewChild, AfterViewInit } from '@angular/core';
import { EnumEditorInputComponent } from '@farris/ui-enum-editor';
import { DatagridBaseEditorDirective } from '../datagrid-base-editor.directive';
import { EnumEditorDefaultOptions } from '../editor-default-options';

@Component({
    selector: 'datagrid-enum-editor',
    template: `
    <div [formGroup]="group" class="f-datagrid-cell-formgroup farris-group-auto">
    <enum-editor-input #eei
        style="width: 100%"
        [formControlName]="column.field"
        [readonly]="options.readonly"
        [editable]="options.editable"
        [disabled]="options.disabled"
        [enableClear]="options.enableClear"
        [outType]="options.outType"
        [textField]="options.textField"
        [valueField]="options.valueField">
    </enum-editor-input>
    </div>
    `
})
export class DatagridEnumEditorComponent  extends DatagridBaseEditorDirective implements OnInit, AfterViewInit {
    @ViewChild('eei') instance: EnumEditorInputComponent;
    constructor(
        render: Renderer2, el: ElementRef, private ngzone: NgZone, public injector: Injector
    ) {
        super(render, el, injector);
    }

    ngOnInit(): void {
        super.ngOnInit();
        this.options = Object.assign( {} , EnumEditorDefaultOptions, this.options);
    }

    ngAfterViewInit() {
        this.inputElement = this.instance.inputGroupRef.textbox.nativeElement;
        super.ngAfterViewInit();
    }
}
