import { Component, OnInit, Renderer2, ElementRef, NgZone, Injector, ViewChild, AfterViewInit } from '@angular/core';

import { SorterComponent } from '@farris/ui-sort-editor';

import { DatagridBaseEditorDirective } from '../datagrid-base-editor.directive';
import { SortEditorDefaultOptions } from '../editor-default-options';

@Component({
    selector: 'datagrid-sort-editor',
    template: `
    <div [formGroup]="group" class="f-datagrid-cell-formgroup farris-group-auto">
        <sorter #sorter
            style="width: 100%"
            [formControlName]="column.field"
            [columns]="options.columns"
            [fieldEditor]="options.fieldEditor"
            [enableClear]="options.enableClear"
            [disabled]="options.disabled"
            [readonly]="options.readonly"
            [editable]="options.editable"
            [returnType]="options.returnType"
        ></sorter>
    </div>
    `
})
export class DatagridSortEditorComponent  extends DatagridBaseEditorDirective implements OnInit, AfterViewInit {

    @ViewChild('sorter') instance: SorterComponent;

    constructor(
        render: Renderer2, el: ElementRef, private ngzone: NgZone, public injector: Injector
    ) {
        super(render, el, injector);
    }

    ngOnInit(): void {
        super.ngOnInit();
        this.options = Object.assign( {} , SortEditorDefaultOptions, this.options);
    }

    ngAfterViewInit() {
        this.inputElement = this.instance.textbox.nativeElement;
        super.ngAfterViewInit();
    }
}
