import { Directive, OnInit, OnDestroy, AfterViewInit, Renderer2, ElementRef, Input, Injector, NgZone, HostBinding} from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { DataColumn, DatagridComponent, ValidatorMessagerService, DatagridFacadeService, DatagridRowDirective } from '@farris/ui-datagrid';

@Directive({
    selector: 'datagrid-editor',
})
export class DatagridBaseEditorDirective implements OnInit, OnDestroy, AfterViewInit {

    @Input() placeholder = '';
    // ç»ä»¶é«åº¦
    @Input() height = '';
    controlId: string;
    type: string;
    options: any = {};
    group: FormGroup;
    column: DataColumn;
    formControl: FormControl;

    inputElement: any;
    /** æ¯å¦æ­£å¨åserver åéè¯·æ± */
    pending = false;

    errorMessage: string;

    /** ç¦æ­¢äºä»¶åæ³¡ */
    stopPropagation = true;
    /** é»è®¤ç¦ç¹ */
    focus = true;

    private clickEvent: any;
    private mouseDownEvent: any;
    private mouseUpEvent: any;
    private dblClickEvent: any;
    vms: ValidatorMessagerService;
    dg: DatagridComponent;
    dfs: DatagridFacadeService;
    validators = [];

    ngZone: NgZone;
    private _inputKeydown = null;

    _editorClickEvent: MouseEvent = null;
    @HostBinding('class') cls = 'datagrid-editor';
    @HostBinding('style.width') width = '100%';
    get dr(): DatagridRowDirective {
        return this.dg.selectedRow.dr;
    }
    private focusTimer = null;
    constructor(public render: Renderer2, public el: ElementRef, public injector: Injector) {
        this.vms = this.injector.get(ValidatorMessagerService);
        this.dg = this.injector.get(DatagridComponent);
        this.dfs = this.injector.get(DatagridFacadeService);
        this.ngZone = this.injector.get(NgZone);
    }

    ngOnInit(): void {

        if (this.column && this.column.editor) {
            this.options = this.column.editor.options;
            this.validators = this.column.editor.validators || [];

            // å¯ç¨ä»»æè¾å¥åï¼å­ç¬¦æå¤§é¿åº¦å±æ§å¼éªè¯
            // å½maxLength <= 0 æ¶è®¤ä¸ºæ­¤å±æ§æ æ
            if (this.options && this.options.nosearch !== undefined && this.options.nosearch) {
                if ( this.options.maxLength !== undefined && this.options.maxLength <= 0) {
                    this.options.maxLength = undefined;
                }
            }
        }

        this.clickEvent = this.render.listen(this.el.nativeElement, 'click', (e: MouseEvent) => {
            e.stopPropagation();
            this.dg['focusElement'] = this.inputElement;
        });

        this.mouseDownEvent = this.render.listen(this.el.nativeElement, 'mousedown', (e: MouseEvent) => {
            this._editorClickEvent = e;
        });

        this.mouseUpEvent = this.render.listen(this.el.nativeElement, 'mouseup', (e: MouseEvent) => {
            this._editorClickEvent = null;
        });


        if (this.group) {
            this.formControl = this.group.controls[this.column.field] as FormControl;
        }
        // this.dblClickEvent = this.render.listen(this.el.nativeElement, 'dblclick', (e: MouseEvent) => {
        //     e.stopPropagation();
        //     e.preventDefault();
        // });

    }

    ngAfterViewInit() {
        this.setFocus();
        if (this['instance']) {
            this['instance'].inDatagrid = true;
        }
        this.setErrorMessage();

        this._inputKeydown =  this.render.listen(this.inputElement, 'keydown', (e) => {
            if (e.ctrlKey || e.shiftKey) {
                e.stopPropagation();
            }
        });

        if (this.formControl) {
            this.formControl.valueChanges.subscribe( (val: any) => {
                // è®°å½åæ´é
                if (!this.formControl.pristine) {
                    const rowId = this.dg.selectedRow ? this.dg.selectedRow.id : '';
                    if (rowId) {
                        const keyField = this.dg.idField;
                        const changeData = { [keyField]: rowId, [this.column.field]: val };
                        this.dfs.appendChanges(changeData);
                    }
                }
                this.setErrorMessage();
            });
        }
    }

    ngOnDestroy() {
        if (this.clickEvent) {
            this.clickEvent();
        }
        if (this.dblClickEvent) {
            this.dblClickEvent();
        }

        if (this.mouseDownEvent) {
            this.mouseDownEvent();
        }
        if (this.mouseUpEvent) {
            this.mouseUpEvent();
        }

        if (this.dg) {
            this.dg['focusElement'] = null;
        }
        if (this._inputKeydown) {
            this._inputKeydown();
            this._inputKeydown = null;
            this.inputElement = null;
        }

    }

    private setErrorMessage() {
        if (this.formControl && this.formControl.invalid) {
            Object.keys(this.formControl.errors).forEach( (key: string) => {
                this.errorMessage = this.vms.getValidatorErrorMessage(key, this.validators);
            });
        }
    }

    private setFocus() {
        if (!this.focus) {
            return;
        }

        if (this.ngZone) {
            this.ngZone.runOutsideAngular(() => {
                if (this.focusTimer) {
                    clearTimeout(this.focusTimer);
                }
                this.focusTimer = setTimeout(() => {
                    if (this.inputElement && this.dg.editMode === 'cell') {
                        if (this.dg && this.dg.selectOnEditing) {
                            if (this.inputElement.select) {
                                this.inputElement.select();
                            }
                        } else {
                            this.inputElement.focus();
                        }

                        this.dg['focusElement'] = this.inputElement;
                    }
                }, 10);
            });
        }
    }


    eventParams = ($event) => {
        let rowData = null;
        let rowId = null;
        if (this.dr) {
            rowData = this.dr.rowData;
            rowId = this.dr.rowId;
        }

        return {
            rowData,
            rowId,
            value: $event,
            form: this.group,
            formControl: this.formControl
        };
    }

    onValueChange($event) {
        if (this.options.valueChange) {
            this.options.valueChange(this.eventParams($event));
        }
    }

    startPending() {
        this.pending = true;
        this.dg.pending = true;
    }

    endPending() {
        this.pending = false;
        this.dg.pending = false;
    }
}
